import { Injectable, Inject } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';

import * as moment_ from 'moment';
const moment = moment_;

import { of, Observable } from 'rxjs';
import { tap, map } from 'rxjs/operators';

import { StorageVars } from './models/storage-vars';

import { AutologinToken } from './models/autologin-token.model';
import { LoginRequest } from './models/login-request';
import { LoginResponse } from './models/login-response';

@Injectable({
    providedIn: 'root'
})
export class TbAuthService {
    public loginUrl: string;
    public errorMessage: string;
    public redirectUrl = '/';

    constructor(@Inject('env') private env, private http: HttpClient, public router: Router) {}

    async isValidToken(autologinToken: AutologinToken = null) {
        const authtoken = localStorage.getItem(StorageVars.JWT);
        console.log('isValidToken - authtoken', authtoken);
        if (!authtoken && !autologinToken) {
            return of(false);
        }

        return this.http
            .post(this.getIsValidTokenUrl(), autologinToken ? autologinToken : {})
            .pipe(
                tap((jObj: any) => {
                    console.log('isValidToken - response', jObj);
                    if (!jObj.Result) {
                        jObj.Message = jObj.Message ? jObj.Message : 'isValidToken error...';

                        this.clearStorage();

                        this.errorMessage = jObj.Message;
                    }
                })
            )
            .toPromise();
    }

    login(loginRequest: LoginRequest): Observable<LoginResponse> {
        return this.http.post(this.getLoginUrl(), loginRequest).pipe(
            map((loginResponse: LoginResponse) => {
                if (!loginResponse.Result) {
                    this.clearStorage();
                    loginResponse.Message = loginResponse.Message ? loginResponse.Message : 'Login error...';
                    this.errorMessage = loginResponse.Message;
                    return loginResponse;
                }

                this.storageData(loginResponse);

                return loginResponse;
            })
        );
    }

    private storageData(loginResponse: LoginResponse) {
        const respCulture: string =
            loginResponse.Culture === undefined || loginResponse.Culture.length === 0 ? window.navigator.language : loginResponse.Culture;
        const respUiCulture: string =
            loginResponse.UICulture === undefined || loginResponse.UICulture.length === 0
                ? window.navigator.language
                : loginResponse.UICulture;
        this.saveCulture(respCulture, respUiCulture);

        localStorage.setItem(StorageVars.JWT, loginResponse.JwtToken);

        const exp = loginResponse.Exp ? moment(loginResponse.Exp) : moment().add(1, 'day');
        localStorage.setItem(StorageVars.EXP, JSON.stringify(exp.valueOf()));
    }

    public getIsValidTokenUrl() {
        return this.getBaseUrl() + 'token/';
    }

    public getLoginUrl() {
        return this.getBaseUrl() + 'tokens/';
        return this.getBaseUrl() + 'login/';
    }

    public getLogoutUrl() {
        return this.getBaseUrl() + 'logout/';
    }

    getRedirectUrl(): string {
        return this.redirectUrl;
    }

    setRedirectUrl(url: string): void {
        this.redirectUrl = url;
    }

    /**
     * Ritorna la base url del backend,
     * caricata da un file di configurazione caricato dinamicamente (assets/environment.json)
     */
    public getBaseUrl() {
        if (this.loginUrl) return this.loginUrl;

        this.loginUrl = this.env.auth.url;

        return this.loginUrl;
    }

    public logoff() {}

    public saveCulture(culture = '', uiCulture = '') {
        localStorage.setItem(StorageVars.CULTURE, culture);
        localStorage.setItem(StorageVars.UI_CULTURE, uiCulture);
    }

    public clearStorage() {
        localStorage.removeItem(StorageVars.JWT);
        localStorage.removeItem(StorageVars.EXP);
        localStorage.removeItem(StorageVars.CULTURE);
        localStorage.removeItem(StorageVars.UI_CULTURE);
    }

    isExpired() {
        const expiration = localStorage.getItem(StorageVars.EXP);
        if (!expiration) return false;

        const expiresAt = JSON.parse(expiration);
        return moment().isAfter(moment(expiresAt));
    }
}
